.select {
  display: inline-block;
  vertical-align: top;
  margin-bottom: 40px; }
.select-wrap {
  max-width: 700px;
  width: 100%;
  margin: 20px auto; }

.select-wrap select {
  margin: 20px; }
.select__gap {
  margin: 50px 0 4px 0;
  border: 1px solid #e2e2e2;
  color: #a7a7a7;
  font-family: "Exo 2", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 30px;
  padding: 10px 0 0 20px;
  cursor: pointer;
  position: relative; }


.on.select__gap,
.select__gap:hover {
  font-family: "Exo 2", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 30px; }


.select__list {
  width: 577px;
  background-color: #ffffff;
  border: 1px solid #e2e2e2;
  margin: 0 0;
  height: 200px;
  overflow-y: scroll;
  position: absolute; }

.select__list.on {
  display: block; }


.select__item span {
  display: block;
  padding: 8px 0 0 20px;
  cursor: pointer;
  color: #444444;
  font-family: "Exo 2", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 15px; }
.select__item:first-child span {
  padding-top: 20px; }
.select__item:last-child span {
  padding-bottom: 15px; }


.select__gap:after {
  content: '';
  display: block;
  width: 9px;
  height: 7px;
  position: absolute;
  right: 20px;
  top: 50%;
  background: url("../img/select-arrow.png");
  margin-top: -4px;

  -webkit-transition: all .27s ease-in-out;
  -o-transition: all .27s ease-in-out;
  transition: all .27s ease-in-out;

  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg); }


.on.select__gap:after {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg); }



 ::-webkit-scrollbar-button {
  background-repeat: no-repeat;
